.pikachu-wrapper {
  height: 450px;
  width: 300px;
}

h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 2em;
  color: #452211;
  font-family: 'Loved by the King', cursive
}

.pikachu{
  top: auto;
  height: 65%;
  width: 140px;
}

.pikachu::before {
  position: absolute;
    content: '';
    background: rgba(0,0,0,0.15);
    height: 10px;
    width: 90%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 95px;
    border-radius: 50%;
}

.pikachu .head {
  bottom: auto;
  height: 120px;
  width: 120px;
  z-index: 3;
}

.pikachu .head .face{
  width: 120px;
  height: 70px;
  top: auto;
  border-radius:  40px 40px 30px 30px;
  z-index: 2;
}

.face .eye {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  bottom: auto;
  top: 28px;
  -webkit-animation: blink 4s infinite;
          animation: blink 4s infinite;
}


.eye.left {
  right: auto;
  left: 32px;
}

.eye.right {
  left: auto;
  right: 32px;
}

.face .mouth {
  text-align: center;
  height: 10px;
}

.face .cachete {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  bottom: auto;
  top: 38px;
  background-color: #e64900;
  z-index: 9;
}

.cachete.left {
  right: auto;
  left: 10px;
}

.cachete.right {
  left: auto;
  right: 10px;
}


.pikachu .head .ears{
  width: 20px;
  height: 90px;
  bottom: auto;
  border-radius: 50%;
  overflow: hidden;
  z-index: 1;
}

.ears.left{
  right: auto;
  left: 10px;
}

.ears.right{
  left: auto;
  right: 10px;
}

.ears .ears-inside{
  top: auto;
  height: 70px;
  width: 30px;
  border-radius: 50%;
}

.ears.right .ears-inside{
  position: absolute;
    left: -50%;
}

.pikachu .body {
  bottom: auto;
  top: 80px;
  height: 105px;
  width: 130px;
  border-radius: 80px 80px 40px 40px;
  overflow: hidden;
  z-index: 2;
}

.pikachu .body .paw{
  bottom: auto;
  top: 50px;
  height: 15px;
  width: 30px;
  z-index: 9;
  border-bottom: 1px solid #e0a100;
  border-top: 1px solid #FFFFFF;
}

.pikachu .body .paw.left{
  right: auto;
  left: 15px;
  border-radius: 0 50% 50% 0;
}

.pikachu .body .paw.right{
  left: auto;
  right: 15px;
  border-radius: 50% 0 0 50%;
}

.pikachu .body .heart {
    width: 40px;
    height: 40px;
    bottom: auto;
    top: 10px;
    z-index: 8;
    animation:beat 0.45s infinite;
    -webkit-animation:beat 0.45s infinite;
}

.pikachu .body .heart:before,
.pikachu .body .heart:after {
    position: absolute;
    content: "";
    left: 20px;
    top: 30px;
    width: 50%;
    height: 100%;
    background: #e64900;
    border-radius: 10px 10px 0 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
.pikachu .body .heart:after {
    left: 0px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 100% 100%;
            transform-origin :100% 100%;
}

.pikachu .leg {
  z-index: 1;
  bottom: auto;
  top: 172px;
  height: 20px;
  width: 30px;
}

.pikachu .leg.left {
  right: auto;
  left: 20px;
  border-radius: 80px 10px 80px 10px;
}

.pikachu .leg.right {
  left: auto;
  right: 20px;
  border-radius: 10px 80px 10px 80px;
}

.pikachu .tail {
  z-index: 1;
  height: 40px;
  width: 50px;
  left:auto;
  bottom: auto;
  top: 70px;
  border-radius: 10% 30%;
}

.signature {
    position: absolute;
    margin: auto;
    bottom: 0;
    top: auto;
}

.signature p{
    text-align: center;
    font-family: Helvetica, Arial, Sans-Serif;
    font-size: 0.85em;
}

.signature .much-heart{
    display: inline-block;
    position: relative;
    margin: 0 4px;
    height: 10px;
    width: 10px;
    background: #AC1D3F;
    border-radius: 4px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.signature .much-heart::before,
.signature .much-heart::after {
      display: block;
  content: '';
  position: absolute;
  margin: auto;
  height: 10px;
  width: 10px;
  border-radius: 5px;
  background: #AC1D3F;
  top: -4px;
}

.signature .much-heart::after {
    bottom: 0;
    top: auto;
    left: -4px;
}

.signature a {
    color: #333;
    text-decoration: none;
    font-weight: bold;
}

